.container{
    min-height: 100%;
    min-width: 100%;
    background-color:black;
    overflow: auto;
    white-space: nowrap;
    position: fixed;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center; 
    transition: opacity 4s;
    opacity: 0;
}
.container .content{
    display: flex;
    align-items: center;  
    margin-left: -65%;
 }

 #padding-left-limit{
     padding-left: 50%;
}

.portrait{
    margin-right: 3vh;
}
.landscape{
    margin-right: 3vh;
}

/* more details square div */
.landscape:hover .square{
    height: 50px;
    opacity: 0.8;
}
.portrait:hover .square{
    height: 50px;
    opacity: 0.8;
}
.landscape:left .square{
    height: 0px;
}
.portrait:left .square{
    height: 0;
}
.landscape .square {
    cursor: pointer;
    transition: height 1s ease;
    height: 0;
    overflow: overlay;
    width: 50px;
    background-color: black;
    position: absolute;
    -webkit-transform: translateY(-90%);
            transform: translateY(-90%);
    margin-left: 450px;
    opacity: 0.8;
}
.portrait .square {
    cursor: pointer;
    transition: height 1s ease;
    height: 0;
    overflow: overlay;
    width: 50px;
    background-color: black;
    position: absolute;
    -webkit-transform: translateY(-90%);
            transform: translateY(-90%);
    margin-left: 250px;
    opacity: 0.8;
  }
/* more details div */

/* overlay full screen image */
.overlay-full-screen
{
    height: 100%;
    width: 100%;
    opacity: 0;
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden;
    transition: 1s opacity ;
    display: none;
}
.overlay-full-screen .closebtn {
    position: absolute;
    right: 20px;
    font-size: 40px;
    cursor: pointer;
    color: #dddd!important;
    opacity: 0.5;
}
.overlay-full-screen .closebtn:hover {
    opacity: 0.8;
}
#full-screen-img.square.landscape{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    margin: auto;
    /*uncoment if I want to set captions on the right */
    /* margin: auto 10% auto; */
    width: 65%;
}
#full-screen-img.square.portrait{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    margin: auto;
    /*uncoment if I want to set captions on the right */
    /* margin: auto 10% auto; */
    width: 30%;
}

