/* CSS for active menu item */
.active-menu-item {
    font-weight: bold; /* You can adjust the font-weight as needed */
}

/* CSS for sticky menu */
.sticky-menu {
    position: absolute;
    top: 0;
    right: 0; /* Align to the top right */
    width: 80%; /* Adjust as needed */
    background-color: black;
    z-index: 999;
    transition: top 0.3s ease-in-out;
  }
  
/* Add this to your CSS file */
.albumContainer {
    overflow-x: auto;
    white-space: nowrap;
    margin-bottom: 5%;
    margin-top: 5%;
  }
  
  .albumContainer img {
    margin-right: 10px;
    max-width: 100%; /* Ensure images don't exceed their container width */
    max-height: 33vh;
    /* height: 33vh; */
  }
  
@media (max-width: 980px)
{
    .categoryContainer {
        position: relative;
      }
      .albumOverlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.7); /* Adjust the opacity as needed */
        display: flex;
        align-items: center;
        justify-content: center;
      }
    .overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.9);
        display: flex;
        align-items: center;
        justify-content: center;
      }
      
      .lightbox img{
        width: 100%;
      }
      
    .container .landscape img
    {
        width: 110vh;
        padding: 2vh;
        background-color:black;
    }
    .container .portrait img
    {
        width: 50vh;
        height: 270px;
    }
    .container .content
    {
        height: 0vh;
        margin-bottom: 10vh;
    }
    .square{
        display: none;
    }
    #my-overlay
    {
        display: none;
    }

    #albumOverlayTitle{
        color: rgba(221, 221, 221, 0.867);
        margin-top: 5px; /* Add margin to separate the title and icon */
        font-size: small;
    }
    #albumOverlayIcon {
        width: 40px; /* Adjust the size as needed */
        height: 40px; /* Adjust the size as needed */
        margin-top: 5px; /* Add margin to separate the title and icon */
      }
      .overlayContent {
        display: flex;
        flex-direction: column;
        align-items: center; /* Center content horizontally */
        text-align: center; /* Center content vertically */
      }
    ::-webkit-scrollbar {
        display: none;
        }
}

@media (max-width: 600px)
{
